import Recognizer from './Recognizer';

/**
 * Detects the end of an utterance and should be used in parallel with a
 * CloudRecognizer.
 * ```
 * var asr = require('jibo').asr;
 * var action = asr.createAudioAction();
 * var cloud = new asr.CloudRecognizer();
 * var endOfSpeech = new asr.EndOfSpeechRecognizer();
 * var task = cloud + " | " + endOfSpeech;
 * action.start(task);
 * ```
 * @class EndOfSpeechRecognizer
 * @private
 * @extends {Recognizer}
 * @memberof module:jibo.services.asr
 * @param {Object} options
 */
class EndOfSpeechRecognizer extends Recognizer {
    constructor(options) {
        options = options || {};
        options.path = "sensory_speech_detector";
        options.speakerId = false;
        super(options);
        this.type = 'eos';
    }

    onResult(data) {
        if(data.status === 'normal') {
            this.emit('result', data);
        }
    }
}

export default EndOfSpeechRecognizer;
